// Import necessary dependencies
import React, { useEffect, useState } from "react";
import { Link } from 'react-scroll';
import { logo, menu, close } from "../assets";
// import { logo } from "../assets";

import { styles } from "../styles";

// Define your component
const Navbar = () => {
    const [active, setActive] = useState("");
    const [toggle, setToggle] = useState(false);
    const [scrolled, setScrolled] = useState(false);
  
    useEffect(() => {
      const handleScroll = () => {
        const scrollTop = window.scrollY;
        if (scrollTop > 100) {
          setScrolled(true);
        } else {
          setScrolled(false);
        }
      };
  
      window.addEventListener("scroll", handleScroll);
  
      return () => window.removeEventListener("scroll", handleScroll);
    }, []);
  
    return (
      <nav
        className={`${
            styles.paddingX
        } w-full flex items-center py-5 fixed top-0 z-20 bg-[#050816] ${
          scrolled ? "bg-primary" : "bg-transparent"
        }`}
      >
        <div className='w-full flex justify-between items-center max-w-7xl mx-auto'>
          <Link
            to='/'
            className='flex items-center gap-2'
            onClick={() => {
              setActive("");
              window.scrollTo(0, 0);
            }}
          >
            <img src={logo} alt='logo' className='w-9 h-9 object-contain' />
            <p className='text-white text-[18px] font-bold cursor-pointer flex '>
              Nur E. &nbsp;
              <span className='sm:block hidden'> | Software Engineer</span>
            </p>
          </Link>
  
          <ul className='list-none hidden sm:flex flex-row gap-10 text-[18px] font-semibold text-[#aaa6c3]'>

        <li style={{cursor: 'pointer'}}>
          <Link
            activeClass="active"
            to="about"
            spy={true}
            smooth={true}
            offset={-70}
            duration={500}
          >
            About
          </Link>
        </li>

        <li style={{cursor: 'pointer'}}>
          <Link
            activeClass="active"
            to="education"
            spy={true}
            smooth={true}
            offset={-70}
            duration={500}
          >
            education
          </Link>
        </li>

        <li style={{cursor: 'pointer'}}>
          <Link
            activeClass="active"
            to="experience"
            spy={true}
            smooth={true}
            offset={-70}
            duration={500}
          >
            experience
          </Link>
        </li>

        <li style={{cursor: 'pointer'}}>
          <Link
            activeClass="active"
            to="my-works"
            spy={true}
            smooth={true}
            offset={-70}
            duration={500}
          >
            my works
          </Link>
        </li>

        <li style={{cursor: 'pointer'}}>
          <Link
            activeClass="active"
            to="extracurricular"
            spy={true}
            smooth={true}
            offset={-70}
            duration={500}
          >
            extracurricular
          </Link>
        </li>

        <li style={{cursor: 'pointer'}}>
          <Link
            activeClass="active"
            to="achievements"
            spy={true}
            smooth={true}
            offset={-70}
            duration={500}
          >
            achievements
          </Link>
        </li>

        <li style={{cursor: 'pointer'}}>
          <Link
            activeClass="active"
            to="contact"
            spy={true}
            smooth={true}
            offset={-70}
            duration={500}
          >
            Contact
          </Link>
        </li>
        
          </ul>
  
          <div className='sm:hidden flex flex-1 justify-end items-center'>
            <img
              src={toggle ? close : menu}
              alt='menu'
              className='w-[28px] h-[28px] object-contain'
              onClick={() => setToggle(!toggle)}
            />
  
            <div
              className={`${
                !toggle ? "hidden" : "flex"
              } p-6 black-gradient absolute top-20 right-0 mx-4 my-2 min-w-[140px] z-10 rounded-xl bg-[#050816]`}
            >
              <ul className='list-none flex justify-end items-start flex-1 flex-col gap-4'>

              <li style={{cursor: 'pointer'}}>
          <Link
            activeClass="active"
            to="about"
            spy={true}
            smooth={true}
            offset={-70}
            duration={500}
          >
            About
          </Link>
        </li>

        <li style={{cursor: 'pointer'}}>
          <Link
            activeClass="active"
            to="education"
            spy={true}
            smooth={true}
            offset={-70}
            duration={500}
          >
            education
          </Link>
        </li>

        <li style={{cursor: 'pointer'}}>
          <Link
            activeClass="active"
            to="experience"
            spy={true}
            smooth={true}
            offset={-70}
            duration={500}
          >
            experience
          </Link>
        </li>

        <li style={{cursor: 'pointer'}}>
          <Link
            activeClass="active"
            to="my-works"
            spy={true}
            smooth={true}
            offset={-70}
            duration={500}
          >
            my works
          </Link>
        </li>

        <li style={{cursor: 'pointer'}}>
          <Link
            activeClass="active"
            to="extracurricular"
            spy={true}
            smooth={true}
            offset={-70}
            duration={500}
          >
            extracurricular
          </Link>
        </li>

        <li style={{cursor: 'pointer'}}>
          <Link
            activeClass="active"
            to="achievements"
            spy={true}
            smooth={true}
            offset={-70}
            duration={500}
          >
            achievements
          </Link>
        </li>

        <li style={{cursor: 'pointer'}}>
          <Link
            activeClass="active"
            to="contact"
            spy={true}
            smooth={true}
            offset={-70}
            duration={500}
          >
            Contact
          </Link>
        </li>

              </ul>
            </div>
          </div>
        </div>
      </nav>
    );
  };

export default Navbar;
